.page-header {

    &--alt {
        h1 {
            margin: 0;
        }

        .page-header__background {
            padding-top: 260px;

            @include media-breakpoint-down(md) {
                padding-top: 120px;
            }
        }
        .page-header__content {
            padding: 35px 0 35px 35px;

            @include media-breakpoint-down(md) {
                padding: 20px;
            }
        }
    }

    &__background {
        position: relative;
        overflow: hidden;
        min-height: 600px;
        padding-top: 350px;

        @include bg-cover;

        @media only screen and (max-width: $screen-xxl) {
            padding-top: 150px;
            min-height: 400px;
        }

        @include media-breakpoint-down(md) {
            padding-top: 150px;
            min-height: 300px;
        }
    }

    &__content {
        position: relative;
        max-width: calc(50% - 90px);
        margin-left: auto;
        padding: 65px 0 160px 200px;

        @include media-breakpoint-down(lg) {
            max-width: 50%;
            padding: 50px 0 50px 50px;
        }

        @include media-breakpoint-down(md) {
            max-width: 100%;
        }

        @include media-breakpoint-down(xs) {
            padding: 20px 0 50px 30px;
        }

        &:after {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            z-index: 0;
            width: calc((((100vw - 200%) / 2) + 100%) - 90px);
            height: 100%;
            background: rgba($white, 0.75);

            @include media-breakpoint-down(lg) {
                width: calc((((100vw - 200%) / 2) + 100%));
            }

            @include media-breakpoint-down(md) {
                width: calc((((100vw - 100%) / 2) + 100%));
            }
        }

        * {
            position: relative;
            z-index: 2;
        }
    }
}

.archive {
    .page-header {
        text-align: center;

        &__background {
            padding-bottom: 100px;
        }
    }
}
