    width: 70px;
  }
}

.burger__menu {
  display: block;
  position: relative;
  margin: 0;
  width: 20px;
  height: 17px;
  z-index: 27;
  cursor: pointer;
}

.burger__lines {
  position: absolute;
  background: white;
  width: 100%;
  height: 3px;
  top: 7px;
  right: 0;
  opacity: 1;
}

.burger__lines:before {
  position: absolute;
  background: white;
  width: 100%;
  height: 3px;
  top: 7px;
  left: 0;
  content: "";
  display: block;
}

.burger__lines:after {
  position: absolute;
  background: white;
  width: 100%;
  height: 3px;
  bottom: 7px;
  left: 0;
  content: "";
  display: block;
}

.burger__lines, .burger__lines::after, .burger__lines::before {
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
}

