.mastfoot {
    position: relative;
    overflow: hidden;
    background: $darkgrey;


    &.mastfoot--tweets {
        .gform_wrapper {
            padding-left: 0;

            .gform_footer {
                text-align: left;
            }
        }

        .mastfoot__text {
            max-width: 300px;
        }

        .mastfoot__form {
            max-width: 400px;
            margin: 0 auto;

            @include media-breakpoint-down(lg) {
                max-width: 100%;
            }
        }
    }

    &--blue-bg {

        .headings {
            * {
                &:first-child {
                    &:before {
                        background: $white;
                    }
                }
            }

        }

        .mastfoot__form, .mastfoot__tweets {
            .text-primary {
                color: $white!important;
            }
        }

        .mastfoot__form {
            padding-left: 40px;

            @include media-breakpoint-down(lg) {
                padding: 0;
            }
        }

        .mastfoot__col--form {
            background: $color-primary;

            &:after {
                content: '';
                display: block;
                position: absolute;
                top: 0;
                left: 0;
                z-index: 0;
                width: calc((((100vw - (100% / (100 / 3 * 2 / 10) * 10)) / 2 - 8px) + 100%));
                height: 100%;
                background: $color-primary;

                @include media-breakpoint-down(lg) {
                    left: -200px;
                    width: calc(100vw + 200px);
                }
            }


            ::placeholder {
                color: $white;
                opacity: 1;
            }

            :-ms-input-placeholder {
                color: $white;
            }

            ::-ms-input-placeholder {
                color: $white;
            }

            .gform_confirmation_wrapper {
                color: $white;
            }

            .gform_wrapper {
                .gform_footer {
                    .btn {
                        border-color: $white;

                        &:hover, &:focus {
                            background: $white;
                            color: $color-primary;
                        }

                    }
                }
            }
        }
    }


    .validation_error {
        display: block;
        text-align: center;
        margin-bottom: 10px;
        font-weight: 600;
        color: $white;
    }

    .validation_message {
        color: $white;
    }


    input, textarea, select {
        border-color: $white;
    }

    .gfield_label {
        color: $white;
    }


    input, textarea, select {
        color: $white;

        + label {
            color: $white;
        }
    }

    select {
        background-image: url(../images/select-arrow-white.svg);
        background-position: calc(100% - 20px) center;
        background-repeat: no-repeat;
        background-size: 15px;
        background-color: transparent;
    }


    .gform_wrapper {
        position: relative;
        z-index: 2;
        padding-bottom: 60px;

        .gform_footer {
            text-align: right;

            .btn {
                background: transparent;
                color: $white;

                &:hover, &:focus {
                    background: $color-primary;
                }
            }
        }
    }


    &__col {
        padding-top: 200px;
        padding-bottom: 25px;

        @include media-breakpoint-down(lg) {
            &:first-child {
                padding-top: 120px;
            }

            &:last-child {
                padding-top: 25px;
            }
        }
    }

    a {
        &:hover, &:focus {
            color: $white;
        }
    }

    &__copy {
        max-width: 400px;
        margin-bottom: 80px;

        @include media-breakpoint-down(lg) {
            margin-bottom: 40px;
        }

        &.copy-small {
            max-width: 300px;
            margin-bottom: 130px;

            @include media-breakpoint-down(lg) {
                margin-bottom: 40px;
            }
        }
    }

    &__copyright {
        color: $midgrey;


        @include media-breakpoint-down(lg) {
            font-size: 18px;
        }
    }

    &__bottom {
        padding: 40px 0;
        background: $black;

        @include media-breakpoint-down(sm) {
            padding: 30px 0;
            text-align: center;
        }

        ul {
            text-align: right;
            @include list-unstyled;
            font-size: 0;
            margin: 0;


            @include media-breakpoint-down(sm) {
                padding-top: 15px;
                text-align: center;
            }
        }

        li {
            display: inline-block;
            padding: 0 15px;
            margin: 0;

            @include media-breakpoint-down(lg) {
                font-size: 18px;
            }

            @include media-breakpoint-down(md) {
                margin-bottom: 10px;
            }
        }

        a {
            display: block;
            padding: 0 20px;
            color: $white;

            @include media-breakpoint-down(lg) {
                padding: 0;
            }

            &:hover, &:focus {
                color: rgba($white, 0.75);
            }
        }
    }

    .social {
        max-width: 350px;

        @include media-breakpoint-down(lg) {
            max-width: 100%;
        }

        ul {
            margin: 0 -15px;

            @include media-breakpoint-down(lg) {
                margin: 0 -5px;
            }
        }

        li {
            margin-bottom: 35px;

            @include media-breakpoint-down(lg) {
                margin-bottom: 10px;
            }


        }

        a {
            width: 70px;
            height: 70px;
            font-size: 32px;

            @include media-breakpoint-down(lg) {
                width: 50px;
                height: 50px;
                font-size: 20px;
            }


            @include media-breakpoint-down(sm) {
                width: 35px;
                height: 35px;
                font-size: 16px;
            }
        }
    }

    &__text {
        position: relative;
        z-index: 2;

    }

    &__form {
        position: relative;
        z-index: 2;
    }

    &__tweets {
        position: relative;
        z-index: 2;
        max-width: 300px;
        margin: 0 auto;

        @include media-breakpoint-down(lg) {
            max-width: 100%;
        }
    }
}
