.tile {

    &__col {
        position: relative;
        overflow: hidden;
    }

    &__link {
        backface-visibility: hidden;
        transform: translate3d(0,0,0);

        &:hover, &:focus {

            .tile__image {
                transform: scale(1.1);

                &:after {
                    opacity: 0.5;
                }
            }

            .btn {
                background: $color-primary;
                color: $white;
            }
        }
    }


    .row {
        margin: 0;

        > div {
            padding: 0;
        }
    }

    p, li {
        color: $black;
    }

    &__image {
        width: 100%;
        height: 100%;
        transition: 600ms;

        @include bg-cover;

        &:after {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: linear-gradient(135deg, $color-primary, rgba($darkgrey, 0.15));
            opacity: 0;
            transition: 600ms;
        }
    }

    &__content {
        padding: 50px;
        max-width: 660px;
        margin: 0 auto;

        @include media-breakpoint-down(lg) {
            padding: 40px;
        }

        @include media-breakpoint-down(xs) {
            padding-right: 10px;
            padding-left: 10px;
        }
    }


}
