.logos {

    .headings {
        padding-bottom: 30px;
    }

    &__carousel {
        margin: 0 -15px;

        @media only screen and (max-width: $screen-xxl) {
            padding: 0 45px;
        }
    }

    &__carousel-item {
        width: 20%;
        padding: 0 15px;
        text-align: center;

        @include media-breakpoint-down(lg) {
            width: 25%;
        }

        @include media-breakpoint-down(md) {
            width: (100% / 3);
        }

        @include media-breakpoint-down(sm) {
            width: 50%;
        }

        @include media-breakpoint-down(xs) {
            width: 100%;
        }
    }
}
