.offices {

    &__carousel {
        margin: 0 -15px;

        @media only screen and (max-width: $screen-xxl) {
            padding: 0 45px;
        }
    }

    &__carousel-item {
        width: 25%;
        padding: 0 15px;

        @include media-breakpoint-down(lg) {
            width: (100% / 3);
        }

        @include media-breakpoint-down(md) {
            width: 50%;
        }

        @include media-breakpoint-down(xs) {
            width: 100%;
        }

        h5 {
            margin-bottom: 0;
            text-transform: uppercase;
        }
    }
}
