.tiles {

    &--one {

        h4 {
            margin-bottom: 10px;
        }

        h6 {
            margin-bottom: 30px;
        }

        p,li, .small {
            color: $midgrey;
        }

        .small {
            display: block;
            padding-top: 30px;
        }

        .button {
            display: none;
        }

        .tiles__blocks {
            display: flex;
            flex-wrap: wrap;
        }

        .tiles__block {
            flex: 0 0 (100% / 3);
            max-width: (100% / 3);

            @include media-breakpoint-down(lg) {
                flex: 0 0 100%;
                max-width: 100%;
            }



            @include media-breakpoint-only(md) {
                &:nth-child(even) {
                    .tile__col {
                        &:first-child {
                            order: 2;
                        }
                    }
                }
            }

            @include media-breakpoint-only(lg) {
                &:nth-child(even) {
                    .tile__col {
                        &:first-child {
                            order: 2;
                        }
                    }
                }
            }

            @include media-breakpoint-up(xl) {
                &:nth-child(6n + 2), &:nth-child(6n + 5) {
                    .tile__col {
                        &:first-child {
                            order: 2;
                        }
                    }
                }
            }


        }


        .tile {
            &__col {
                flex: 0 0 100%;
                max-width: 100%;

                @include media-breakpoint-down(lg) {
                    flex: 0 0 50%;
                    max-width: 50%;;
                }

                @include media-breakpoint-down(sm) {
                    flex: 0 0 100%;
                    max-width: 100%;
                }

                &--content {
                    display: flex;
                    flex-wrap: wrap;
                    align-items: flex-end;
                    height: 550px;

                    @include media-breakpoint-down(lg) {
                        height: 450px;
                    }

                    @include media-breakpoint-down(md) {
                        height: auto;
                    }
                }
            }

            &__content {
                flex: 0 0 100%;
                max-width: 100%;
                padding-bottom: 100px;

                @include media-breakpoint-down(md) {
                    padding: 40px;
                    min-height: 350px;
                }

                @include media-breakpoint-down(sm) {
                    padding: 30px 20px 50px;
                    min-height: 0;
                }
            }

            &__image {
                height: 550px;

                @include media-breakpoint-down(lg) {
                    height: 450px;
                }

                @include media-breakpoint-down(md) {
                    height: 100%;
                }

                @include media-breakpoint-down(sm) {
                    height: 200px;
                }
            }
        }
    }

    &--two {
        .tile {

            &__col {
                min-height: 700px;

                @include media-breakpoint-down(md) {
                    min-height: 0;
                }
            }

            &__content {
                @include media-breakpoint-down(md) {
                    padding-bottom: 60px;
                }
            }

            &__image {
                @include media-breakpoint-down(md) {
                    height: 300px;
                }
            }
        }

        .button {
            padding-top: 30px;
        }

        .tiles__block {
            &:nth-child(even) {
                .tile__link {
                    > .row {
                        flex-direction: row-reverse;
                    }
                }
            }
        }
    }
}
