.headings {
    margin-bottom: 60px;

    @include media-breakpoint-down(lg) {
        margin-bottom: 45px;
    }

    @include media-breakpoint-down(sm) {
        margin-bottom: 35px;
    }


    > * {
        margin-bottom: 0;

        &:first-child {
            &:before {
                content: '';
                display: block;
                width: 35px;
                height: 4px;
                margin-bottom: 15px;
                background: $color-primary;
            }
        }
    }

    .text-primary {
        color: $color-primary;
    }
}
