.hero {

    &__slide {
        width: 100%;
    }

    &__background {
        position: relative;
        padding-top: 170px;
        padding-bottom: 260px;
        height: 100%;
        @include bg-cover;

        @include media-breakpoint-down(md) {
            padding-top: 140px;
            padding-bottom: 200px;
        }

        @include media-breakpoint-down(sm) {
            padding-top: 130px;
            padding-bottom: 130px;
        }
    }

    &__video {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        iframe {
            box-sizing: border-box;
            height: 56.25vw;
            left: 50%;
            min-height: 100%;
            min-width: 100%;
            transform: translate(-50%, -50%);
            position: absolute;
            top: 50%;
            width: 500vh;
        }
    }

    &__content {
        max-width: 50%;
        margin-left: auto;

        @media only screen and (max-width: $screen-xxl) {
            max-width: 75%;
        }

        @include media-breakpoint-down(md) {
            max-width: 100%;
        }
    }

    &__title {
        position: relative;
        padding: 50px 0 50px 120px;

        @media only screen and (max-width: $screen-xxxl) {
            padding-left: 50px;
        }

        @include media-breakpoint-down(md) {
            padding: 35px;
        }


        @include media-breakpoint-down(xs) {
            padding: 20px;
        }

        h1 {
            position: relative;
            z-index: 1;
            margin: 0;
            @media only screen and (max-width: $screen-xxs) {
                @include font-size(35px);
            }
        }

        &:after {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            z-index: 0;
            width: calc(((100vw - 150%) / 2) + 100%);
            height: 100%;
            background: rgba($white, 0.75);

            @media only screen and (max-width: $screen-xxl) {
                width: calc(((100vw - 125%) / 2) + 100%);
            }

            @include media-breakpoint-down(md) {
                width: calc(((100vw - 100%) / 2) + 100%);
            }
        }
    }

    .button {
        padding-top: 100px;
        text-align: right;

        @include media-breakpoint-down(sm) {
            padding-top: 50px;
        }
    }
}
